<template>
  <div>
    <dashboard-table
      v-if="items.length > 0" class="mt-4"
      title="Finalize o atendiemento"
      :headers="headers"
      :items="items"
      :loading="loading"
      :noPagination="true"
      @callAction="callAction"
    />
    <dashboard-refuse-schedule-dialog
      ref="dashboardRefuseScheduleDialog" 
      @onConfirmRefuse="cancel"
    />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import DashboardRefuseScheduleDialog from './DashboardRefuseScheduleDialog'
import DashboardTable from './DashboardTable';
import Overlay from '@/components/globais/overlay/Overlay'
import BarberMixin from '@/mixins/BarberMixin'
import DateMixin from '@/mixins/DateMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';
import TextMixin from '@/mixins/TextMixin';
import OverlayMixin from '@/mixins/OverlayMixin'
export default {
  components: { DashboardRefuseScheduleDialog, DashboardTable, Overlay },
  mixins: [ BarberMixin, DateMixin, OverlayMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    headers: [
      { text: 'N', value: 'id', class: 'text-center' },
      { text: '', value: 'image', class: 'text-center', type: 'image' },
      {
        text: 'Nome do cliente',
        value: 'client',
        class: 'text-left',
        type: null,
      },
      { text: 'Data', value: 'date', class: 'text-left' },
      { text: 'Servios', value: 'services', class: 'text-left', type: 'list' },
      { text: 'Valor', value: 'value', class: 'text-left' },
      { text: '#', value: 'actions', class: 'text-center', type: 'action' }
    ],
    items: [],
    interval: null,
    loading: false
  }),
  mounted () {
    this.getSchedules()
    const interval = 1000 * 60 * 5
    this.interval = setInterval(() => {
      this.getSchedules()
    }, interval);
  },
  beforeDestroy () {
    clearInterval(this.interval)
  },
  methods: {
    callAction (action, schedule) {
      switch (action) {
        case 'finish':
          this.finish(schedule.id)
          break;
        case 'cancel':
          this.$refs.dashboardRefuseScheduleDialog.show(schedule)
          break;
      }
    },
    cancel (schedule, cancellationReason) {
      const errorMessage = 'No foi possvel reprovar o agendamento!'
      this.$axios.put(`/schedule/${schedule.id}/repprove`, {
        message: cancellationReason
      }).then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            this.showSnackbar(response.data.corpo)
            const index = this.items.findIndex(i => i.id === schedule.id)
            if (index > -1) this.items.splice(index, 1)
            this.$refs.overlay.hideOverlaySuccess('Agendamento finalizado com sucesso!')
          } else this.setError(response.data.corpo)
        } else this.setError(errorMessage)
      }).catch (() => this.setError(errorMessage))
    },
    finish (schedule_id) {
      this.$refs.overlay.showOverlay('Salvando...')
      const errorMessage = 'No foi possvel finalizar o agendamento!'
      this.$axios.put(`/schedule/${schedule_id}/finish`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            this.$refs.overlay.hideOverlaySuccess('Agendamento finalizado com sucesso!')
            const index = this.items.findIndex(i => i.id === schedule_id)
            if (index > -1) this.items.splice(index, 1)
            this.$emit('onScheduleFinished')
          } else this.setError(response.data.corpo)
        } else this.setError(errorMessage)
      }).catch (() => this.setError(errorMessage))
    },
    getSchedules() {
      const errorMessage = 'No foi possvel buscar os agendamentos!';
      this.loading = true;
      this.$axios.get(`/schedule/barber/${this.barberState.id}/waiting-to-finish`)
      .then((response) => {
        this.loading = false;
        if (response.status === 200) {
          if (Number(response.data.codigo) === 200) {
            this.items = response.data.corpo.map((item) => {
              const services = item.services.map((service) => {
                return `${service.name}`;
              });
              return {
                id: item.id,
                image: item.user.image_url,
                client: item.user.name,
                date: this.formatDateTimeToStringPtBr(
                  new Date(item.start_date)
                ),
                services: services,
                value: `R$ ${this.setMoneyMask(
                  Number(item.price).toFixed(2)
                )}`,
                status: '',
                actions: [
                  { icon: 'check', class: 'success text-none', text: 'Finalizado', action: 'finish' },
                  { icon: 'close', class: 'danger text-none', text: 'Cancelado', action: 'cancel' }
                ]
              };
            });
          } else this.setError(response.data.corpo);
        } else this.setError(errorMessage);
      }).catch(() => this.setError(errorMessage));
    },
    setError(error) {
      this.loading = false
      this.$refs.overlay.hideOverlay()
      this.showSnackbarErro(error)
    }
  }
}
</script>