<template>
  <div class="schedules">
    <v-row>
      <v-col cols="12" sm="8" md="9">
        <dashboard-choose-barber
          class="same-height"
          :schedules="qtdSchedules"
          @changeBarber="changeBarber"
        />
      </v-col>
      <v-col cols="12" sm="4" md="3">
        <dashboard-barber-revenues
          v-show="barberId"
          class="same-height"
          :value="schedulesTotalValue"
        />
      </v-col>
      <template v-if="barberId">
        <v-col cols="12">
          <dashboard-schedules-waiting-to-finish @onScheduleFinished="getSchedulesTotalValue" />
        </v-col>
        <v-col cols="12" class="mt-4">
          <dashboard-table
            title="Clientes agendados"
            :headers="headers"
            :items="items"
            :loading="loading"
            :page="currentPage"
            :qtdPages="pages"
            @changePage="changePage"
            @callAction="callAction"
          />
        </v-col>
      </template>
    </v-row>
  </div>
</template>

<script>
import DashboardBarberRevenues from './DashboardBarberRevenues';
import DashboardChooseBarber from './DashboardChooseBarber';
import DashboardSchedulesWaitingToFinish from './DashboardSchedulesWaitingToFinish'
import DashboardTable from './DashboardTable';
import BarbershopMixin from '@/mixins/BarbershopMixin'
import DashboardMixin from '@/mixins/DashboardMixin'
import DateMixin from '@/mixins/DateMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';
import TextMixin from '@/mixins/TextMixin';
export default {
  name: 'DashboardSchedules',
  components: { DashboardBarberRevenues, DashboardChooseBarber, DashboardSchedulesWaitingToFinish, DashboardTable },
  mixins: [ BarbershopMixin, DashboardMixin, DateMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    barberId: null,
    currentPage: null,
    headers: [
      { text: 'N', value: 'id', class: 'text-center' },
      { text: '', value: 'image', class: 'text-center', type: 'image' },
      {
        text: 'Nome do cliente',
        value: 'client',
        class: 'text-left',
        type: null,
      },
      { text: 'Data', value: 'date', class: 'text-left', type: null },
      { text: 'Servios', value: 'services', class: 'text-left', type: 'list' },
      { text: 'Valor', value: 'value', class: 'text-left', type: null }
    ],
    interval: null,
    items: [],
    loading: true,
    pages: 1,
    qtdSchedules: 0,
    schedulesTotalValue: '0.00'
  }),
  watch: {
    periodState () {
      this.update()
    }
  },
  mounted () {
    this.getSchedules()
  },
  methods: {
    callAction(schedule) {
      window.console.log('callAction', schedule)
    },
    changeBarber(barberId) {
      this.barberId = barberId;
      this.update()
    },
    changePage(page) {
      this.currentPage = page
      this.getSchedules()
    },
    getSchedules() {
      const errorMessage = 'No foi possvel buscar os agendamentos!';
      const endDate = this.periodState.endDate || null
      const startDate = this.periodState.startDate || null 
      let url = `/barber/${this.barberId}/barbershop/${this.barbershop.id}/schedules?start_date=${startDate}&end_date=${endDate}`
      if (this.currentPage) url = `${url}&page=${this.currentPage}`
      this.loading = true;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          if (response.status === 200) {
            if (Number(response.data.codigo) === 200) {
              this.currentPage = response.data.corpo.current_page;
              this.pages = response.data.corpo.last_page;
              this.qtdSchedules = response.data.corpo.total;
              this.items = response.data.corpo.data.map((item) => {
                const services = item.services.map((service) => {
                  return `${service.name}`;
                });
                return {
                  id: item.id,
                  image: item.user.image_url,
                  client: item.user.name,
                  date: this.formatDateTimeToStringPtBr(
                    new Date(item.start_date)
                  ),
                  services: services,
                  value: `R$ ${this.setMoneyMask(
                    Number(item.price).toFixed(2)
                  )}`,
                  status: '',
                };
              });
            } else this.setError(response.data.corpo);
          } else this.setError(errorMessage);
        })
        .catch(() => this.setError(errorMessage));
    },
    getSchedulesTotalValue() {
      let url = `barber/${this.barberId}/barbershop/${this.barbershop.id}/revenues?`;
      if (this.periodState.startDate) url += `start_date=${this.periodState.startDate}&`
      if (this.periodState.endDate) url += `end_date=${this.periodState.endDate}`
      this.$axios.get(url).then((response) => {
        if (Number(response.status) === 200 && Number(response.data.codigo) === 200) {
          this.schedulesTotalValue = `${response.data.corpo.toFixed(2)}`;
        }
      })
    },
    setError(error) {
      this.showSnackbarErro(error);
      this.loading = false;
    },
    update () {
      this.getSchedules();
      this.getSchedulesTotalValue();
    }
  },
};
</script>

<style scoped>
.same-height { display: flex; height: 100%; flex-direction: column; }
@media all and (max-width: 1263px) {
  .same-height { height: auto; }
}
</style>
